<template>
  <div class="CustomerDataDetailsEdit">
    <HeaderNavigation />
    <div class="CustomerDataDetailsEditBox">
      <h1><i></i>編輯備註</h1>
      <textarea v-model="remark" placeholder="請輸入備註"></textarea>
      <div @click="submit()">儲存</div>
    </div>
  </div>
</template>

<script>
import { POST_UpdateCustomerRemark } from "@/utils/api";
import HeaderNavigation from "@/components/HeaderNavigation";
export default {
  name: "CustomerDataDetailsEdit",
  components: {
    HeaderNavigation,
  },
  data() {
    return {
      remark: this.$route.query.Remark,
    };
  },
  methods: {
    submit() {
      if (this.remark == "") {
        return this.$toast.fail("請填寫備註");
      }
      var UofIds = {
        DistributionUofId: sessionStorage.getItem("DistributionUofId"),
        CustId: this.$route.query.id,
        Remark: this.remark,
      };
      POST_UpdateCustomerRemark(UofIds).then((response) => {
        if (response.State) {
          this.$toast.success(response.Msg);
          this.$router.go(-1);
        } else {
          this.$toast.fail(response.Msg);
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.CustomerDataDetailsEdit {
  width: 100%;
  min-height: 100vh;
  background: rgba(255, 255, 255, 1);
  .CustomerDataDetailsEditBox {
    padding: 30px 8% 0;
    h1 {
      font-size: 18px;
      font-weight: 500;
      color: rgba(61, 61, 61, 1);
      line-height: 25px;
      display: flex;
      align-items: center;
      i {
        display: inline-block;
        margin-right: 8px;
        width: 4px;
        height: 4px;
        background: rgba(254, 96, 59, 1);
        border-radius: 50%;
      }
    }
    textarea {
      font-size: 14px;
      resize: none;
      width: 87.34%;
      height: 138px;
      background: rgba(249, 249, 250, 1);
      border-radius: 12px;
      margin-top: 18px;
      margin-bottom: 110px;
      padding: 16px 6.33%;
    }
    div {
      font-size: 16px;
      font-weight: 500;
      color: rgba(255, 255, 255, 1);
      line-height: 50px;
      height: 50px;
      background: linear-gradient(
        180deg,
        rgba(28, 118, 254, 1) 0%,
        rgba(28, 118, 254, 1) 100%
      );
      box-shadow: 0px 10px 40px 0px rgba(28, 118, 254, 0.3);
      border-radius: 12px;
      width: 100%;
      text-align: center;
    }
  }
}
</style>
